//                   SimpTip v1.0.4                  \\
//       A simple CSS tooltip made with Sass         \\
//-----------------------------------------------------
// source: https://github.com/arashmanteghi/simptip/
// ducoment: http://arashm.net/lab/simptip/
// by @ArashManteghi - http://arashm.net
