#conteudo_page {
    width: 100%;
    height: auto;
    float: left;
    margin: 0 0 0 0;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#conteudo_page_int {
    width: 100%;
    height: auto;
    float: left;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

.responsive {
    width: 100%;
    max-width: 1160px;
    margin: 0 auto;
    height: auto;
}

#load {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
    top: 0;
    left: 0;
    background: #fff;
    position: fixed;
    z-index: 99999999;
    display: none;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#load i {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    background: url(../imgs/logo.png) center no-repeat;
}

#load .carregando {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 65px 0 0 20px;
    padding-left: 20px;
    font-size: 12px;
    color: #000;
}

/*LIGHTBOX*/

#lt {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(46, 57, 58, 0.98);
    z-index: 999999;
    overflow: hidden;
    display: none;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#lt_int {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#area_container_lt {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
    overflow: hidden;
}

#top_lt {
    width: 100%;
    height: 53px;
    float: left;
    margin: 0;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 99999;
    background: #0E0E0D;
}

#infos_top_lt {
    width: 50%;
    height: auto;
    float: left;
    margin: 0;
    padding: 20px;
}

#infos_top_lt span {
    width: auto;
    height: 12px;
    float: left;
    margin: 0 20px 0 0;
    padding: 0;
    font-size: 12px;
    line-height: 12px;
    color: #888888;
    font-weight: 400;
    text-align: left;
}

#infos_top_lt h5 {
    width: auto;
    height: 16px;
    float: left;
    margin: -3px 0 0 0;
    padding: 0;
    color: #fff;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    text-align: left;
}

#infos_top_lt p {
    width: 100%;
    height: 12px;
    float: left;
    margin: 5px 0 0 0;
    padding: 0;
    font-size: 12px;
    line-height: 12px;
    color: #FF8000;
    font-weight: 300;
    text-align: left;
}

/*video*/

#lt_page {
    width: 100%;
    height: auto;
    float: left;
    margin: 0;
    padding: 0 0 0 0;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.9);
    z-index: 9999999;
    overflow: hidden;
    display: none;
    font-family: Raleway, sans-serif;
    font-size: 14px;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#lt_int_page {
    width: 100%;
    height: auto;
    float: left;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

#lt_int_page .content_responsive {
    max-width: 960px;
}

#content_lt {
    width: 100%;
    max-width: 854px;
    height: auto;
    float: left;
    margin: 0 0 0 0;
    padding: 0;
    border: 0;
    border-radius: 3px;
    position: relative;
    overflow: hidden;
    -webkit-tap-highlight-color: transparent;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
}

.video-container {
    position: relative;
    padding-bottom: 56.25%;

    height: 0;
}

.video-container iframe,
.video-container object,
.video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#close {
    width: 24px;
    height: 24px;
    float: right;
    background: url(../imgs/fechar.png) center no-repeat;
    -webkit-background-size: 18px 18px;
    background-size: 18px 18px;
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 15px;
}

/*lightbox galeria*/

#top_lt {
    width: 100%;
    height: 53px;
    float: left;
    margin: 0;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 99999;
    background: #0E0E0D;
}

#infos_top_lt {
    width: calc(100% - 100px);
    height: auto;
    float: left;
    margin: 0;
    padding: 20px;
}

#infos_top_lt span {
    width: auto;
    height: 12px;
    float: left;
    margin: 0 20px 0 0;
    padding: 0;
    font-size: 12px;
    line-height: 12px;
    color: #888888;
    font-weight: 400;
    text-align: left;
}

#infos_top_lt h5 {
    width: auto;
    height: 16px;
    float: left;
    margin: -3px 0 0 0;
    padding: 0;
    color: #fff;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    text-align: left;
}

#infos_top_lt p {
    width: 100%;
    height: 12px;
    float: left;
    margin: 5px 0 0 0;
    padding: 0;
    font-size: 12px;
    line-height: 12px;
    color: #FF8000;
    font-weight: 300;
    text-align: left;
}

#top_lt a {
    width: 18px;
    height: 18px;
    float: right;
    background: url(../imgs/fechar.png) center no-repeat;
    -webkit-background-size: 18px 18px;
    background-size: 18px 18px;
    margin: 18px 15px 0 0;
}

#slider_lt {
    width: 100%;
    height: -webkit-calc(100% - 50px);
    height: -moz-calc(100% - 50px);
    height: calc(100% - 50px);
    float: left;
    margin: 50px 0 0 0;
    /*background:rgba(46,57,58, 0.98);*/
    position: fixed;
    top: 0;
    left: 0;
}

#slider_lt li,
#slider_lt li div {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
}

#slider_lt li div img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    float: left;
    margin: 0;
}

#navs_slider_lt {
    width: 100%;
    height: 0px;
    float: left;
    margin: -50px 0 0 0;
    top: calc(50% + 50px);
    left: 0;
    padding: 0 150px;
    position: fixed;
    z-index: 1000;
}

#navs_slider_lt div a {
    width: 40px;
    height: 60px;
    float: left;
    margin: 0;
    background: #55C4CF;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

#navs_slider_lt div a:last-child {
    float: right;
}

#navs_slider_lt div a:hover {
    background: #fff;
}

#navs_slider_lt div a svg {
    width: 30px;
    height: 60px;
    float: left;
    margin: 0 0 0 4px;
}

#navs_slider_lt div a path {
    fill: #FFF;
    cursor: pointer;
}

#navs_slider_lt div a:hover path {
    fill: #222;
}

#area_controls_lt {
    width: 100%;
    height: 50px;
    float: left;
    margin: 0;
    position: fixed;
    bottom: 0;
    left: 0;
}

#area_miniaturas {
    width: 100%;
    height: 130px;
    float: left;
    margin: -130px 0 0 0 !important;
    background: #0E0E0D;
    position: fixed;
    bottom: 0;
    left: 0;
}

#container_fotos_miniaturas {
    width: -webkit-calc(100% - 30px);
    width: -moz-calc(100% - 30px);
    width: calc(100% - 30px);
    height: 101px;
    float: left;
    margin: 15px 0 0 15px;
    padding: 0 !important;
    left: 0px;
    position: absolute;
    z-index: 0;
    overflow: hidden;

    /* Prevent native touch events on Windows */
    -ms-touch-action: none;

    /* Prevent the callout on tap-hold and text selection */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    /* Prevent text resize on orientation change, useful for web-apps */
    -webkit-text-size-adjust: none;
    -moz-text-size-adjust: none;
    -ms-text-size-adjust: none;
    -o-text-size-adjust: none;
    text-size-adjust: none;
}

#scroll_container_fotos_miniaturas {
    position: absolute;
    width: 5000px;
    height: auto;
    float: left;

    /* Prevent elements to be highlighted on tap */
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    /* Put the scroller into the HW Compositing layer right from the start */
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
}

.iScrollHorizontalScrollbar {
    height: 2px !important;
    border: none !important;
    left: 0 !important;
    right: 0 !important;
    /*top:0 !important;*/
    bottom: 0 !important;
    background: rgba(255, 255, 255, 0.7) !important;
    padding: 0 !important;
}

.iScrollHorizontalScrollbar .iScrollIndicator {
    height: 2px !important;
    border: none !important;
    background: #55C4CF !important;
    -webkit-border-radius: 2px !important;
    -moz-border-radius: 2px !important;
    border-radius: 2px !important;
}

#scroll_container_fotos_miniaturas a {
    width: auto;
    height: auto;
    float: left;
    margin: 0 10px 0 0;
    opacity: 0.3;
}

#scroll_container_fotos_miniaturas a.ativo,
#scroll_container_fotos_miniaturas a:hover {
    opacity: 1;
}

#scroll_container_fotos_miniaturas a img {
    width: auto;
    height: 85px;
    float: left;
    margin: 0;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

#load {
    width: 100%;
    height: 100%;
    float: left;
    margin: 0;
    top: 0;
    left: 0;
    background: #fff;
    position: fixed;
    z-index: 99999999;
    display: none;
}

#load i {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    background: url(../imgs/logo.png) center no-repeat;
}

#load .carregando {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 65px 0 0 20px;
    padding-left: 20px;
    font-size: 12px;
    color: #000;
}

@media only screen and (max-width: 1023px) {

    #top_lt a {
        text-indent: -9999px;
        width: 32px;
        height: 32px;
        margin: 0 0 0 0;
    }

    #area_controls_lt {
        display: none;
    }

    #navs_slider_lt {
        display: none;
    }

}

@media only screen and (max-width: 960px) {

    #top_lt {
        height: 0;
    }

    #infos_top_lt {
        display: none;
    }

    #top_lt a {
        margin: 15px 15px 0 0;
    }

    #slider_lt {
        height: 100%;
        margin: 0;
    }

}